import React, { FunctionComponent } from 'react';
import styled from "@emotion/styled"
import { keyframes } from '@emotion/react';

const MainNavContainer = styled.div`
  position: fixed; top: 0; left: 0; right: 0;
  height: 110px;
  background-color: #fff;
  border-bottom: solid 1px #ebe8e8;
`

const MainNavWrap = styled.div`
  width: 100%; height: 100%;
  margin: 0 auto;

  @media (min-width: 1200px) {
    max-width: 1200px;
    display:flex; align-items: center;
  }
`



const MainNavWrapL = styled.div`
  width: 100%; height: 100%;
  display: flex; flex-direction: column;

  @media (min-width: 1200px) {
    flex-direction: inherit; align-items: center;
  }
`
const MainNavWrapR = styled.div`
  position: absolute;
  top: 20px; right: 0;
  width: 150px;
  text-align: center;

  @media (min-width: 1200px) {
    position: static;
  }
`

const LogoImage = styled.img`
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
  width: 70px;
  height: 23px;
  @media (min-width: 1200px) {
    margin-left: 0px;
    margin-top: 0px;
    width: 120px; height: 40px;
  }
`
const NavMenuWrap = styled.ul`
  display: flex;
  width: 100%;height: 100%;
  font-size : 13px;
`
const NavMenuList = styled.li`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20%; height: 100%;
  text-align: center;
  z-index: 10;
  @media (min-width: 1200px) {
    font-size: 18px;
  }
`
const Hop = keyframes`
  0% {top: -5px;}
  100% {top: -10px;}
`
const Hop1200 = keyframes`
  0% {top: 20px;}
  100% {top: 15px;}
`;

const NavStyle = styled.div`
  position: absolute;
  top: -5px; left : 50%;
  transform: translateX(-50%);
  animation: ${Hop} 0.5s linear 1s infinite alternate;

  @media (min-width: 1200px) {
    font-size: 18px;
    top: 20px;
    animation: ${Hop1200} 0.5s linear 1s infinite alternate;
  }
`
const MainNav: FunctionComponent = function(){

  const navMenuList = ["ME", "플러스", "랭킹", "아레나", "내서재"]
  const navMenu = navMenuList.map((menu)=>(
    (menu === "플러스") 
      ? <NavMenuList><NavStyle>new</NavStyle>{menu}</NavMenuList> 
      : <NavMenuList>{menu}</NavMenuList>
  ))

  return (
    <MainNavContainer>
      <MainNavWrap>
        <MainNavWrapL>
          <LogoImage src="image/logo/logo.png" />
          <NavMenuWrap>
            {navMenu}
          </NavMenuWrap>
        </MainNavWrapL>
        <MainNavWrapR>
          로그인이나 그외
        </MainNavWrapR>
      </MainNavWrap>
    </MainNavContainer>
  )
}

export default MainNav